import React, { useEffect, useRef } from "react";
import { CodeIcon, MapIcon } from "assets/icons";
import KeyPoints from "./KeyPoints";
import { KeyPointsBG } from "assets/blobs";

import styles from "./AboutMe.module.scss";

const AboutMe = () => {
  const containerRef = useRef<HTMLDivElement>(null);
  const scrollTipRef = useRef<HTMLHeadingElement>(null);

  useEffect(() => {
    if (!containerRef.current || !scrollTipRef.current) return;
    const options = {
      root: null,
      threshold: 0,
      rootMargin: "0px",
    };
    const observer = new IntersectionObserver((entries, observer) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) return;
        if (scrollTipRef.current)
          scrollTipRef.current.classList.add(styles.disappear);
        if (containerRef.current) observer.unobserve(containerRef.current);
      });
    }, options);
    observer.observe(containerRef.current);
    return () => {
      observer.disconnect();
    };
  }, []);

  return (
    <section className={styles.container} id="aboutme" ref={containerRef}>
      <div className={styles.leftSide}>
        <h1>Hello,</h1>
        <h2>Welcome to my portfolio</h2>
        <section className={styles.card}>
          <p>
            I am <strong>Native Android / iOS developer by profession</strong> with hands-on experience on{" "}
            <strong>Frontend applications based on MERN Stack</strong>.
          </p>
          <div className={styles.codingTools}>
            <CodeIcon />
            <span>Android, iOS, React, TypeScript</span>
          </div>
          <div className={styles.designTools}>
            <CodeIcon />
            <span>Python, Flask &amp; MongoDB</span>
          </div>
          <div className={styles.location}>
            <MapIcon />
            <span>New Delhi, India</span>
          </div>
        </section>
        <h3 ref={scrollTipRef} className={styles.scrollTip}>
          Scroll to view more
        </h3>
      </div>
      <div className={styles.rightSide}>
        <KeyPoints />
        <KeyPointsBG className={styles.blobBg} />
      </div>
    </section>
  );
};

export default AboutMe;
