import React, { useEffect, useRef } from "react";
import { PhoneIcon, LaptopIcon, PublicIcon } from "assets/icons";
import KeyPoint from "./KeyPoint";

import styles from "./KeyPoints.module.scss";

const CARD_ANIM_DURATION = 4000;

const KeyPoints = () => {
  const containerRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (!containerRef.current) return;
    const cards = Array.from(containerRef.current.children);
    function startAnim() {
      cards.forEach((card, index) => {
        setTimeout(() => {
          card.classList.add(styles.animateSlide);
          setTimeout(() => {
            card.classList.remove(styles.animateSlide);
          }, CARD_ANIM_DURATION);
        }, index * CARD_ANIM_DURATION);
      });
    }
    startAnim();
    const interval = setInterval(() => {
      startAnim();
    }, cards.length * CARD_ANIM_DURATION);

    return () => clearInterval(interval);
  }, []);

  return (
    <section className={styles.container}>
      <div className={styles.keypoints} ref={containerRef}>
        <KeyPoint
          title="Mobile Apps"
          line="Built Qandle Android &amp; iOS apps, also Qandle FFT application"
          icon={<PhoneIcon />}
        />
        <KeyPoint
          title="Web Apps"
          line="Built Qanmailer, Mailtrail chrome plugin. more details on projects section"
          icon={<PublicIcon />}
        />
        <KeyPoint
          title="Windows Apps"
          line="Built native mac, window, linux application to track employee productivity"
          icon={<LaptopIcon />}
        />
      </div>
    </section>
  );
};

export default KeyPoints;
