import React from "react";
import { createTool } from "utils";
import Heading from "./Heading";
import ToolTile from "./Tool";

import styles from "./Tools.module.scss";

const tools = [
  createTool("Android", 9,"mobile"),
  createTool("iOS", 8,"mobile"),
  createTool("React", 8),
  createTool("TypeScript", 8),
  createTool("CSS", 7),
  createTool("Sass", 7),
  createTool("Flask", 7, "back"),
  createTool("Redux", 6),
  createTool("Vue", 5),
  createTool("AWS Server", 8, "back"),
  createTool("NodeJS", 5, "back"),
  createTool("MongoDB", 8,"database"),
];

const Tools = () => {
  return (
    <section id="tools">
      <Heading dir="right">Tools</Heading>
      <div className={styles.tools}>
        <div className={styles.type}>
          {tools.map((tool) => (
            <ToolTile tool={tool} key={tool.name} />
          ))}
        </div>
      </div>
    </section>
  );
};

export default Tools;
