import React from "react";
import {
  ReactDynamicChartsImg,
} from "assets/images";
import { OtherProjectType } from "types/OtherProjectType";
import { createOtherProject } from "utils";
import {
  REACT_DYNAMIC_CHARTS,
} from "../constants";

import styles from "./OtherProjects.module.scss";

const otherProjects: OtherProjectType[] = [
  createOtherProject("React Dynamic Charts", ReactDynamicChartsImg, REACT_DYNAMIC_CHARTS),
];

const OtherProjects = () => {
  return (
    <section className={styles.container}>
      <h1>Some More</h1>
      <div className={styles.grid}>
        {otherProjects.map((op, index) => (
          <a
            href={op.link}
            className={styles.opContainer}
            key={index}
            target="_blank"
            rel="noopener noreferrer"
          >
            <img src={op.image} alt={`${op.title}-op`} />
            <div className={styles.backdrop}>
              <span>{op.title}</span>
            </div>
          </a>
        ))}
      </div>
    </section>
  );
};

export default OtherProjects;
