import React from "react";
import { PinkBlob, BlueBlob, LightBlueBlob, GreenBlob } from "assets/blobs";
import { QandleAndroidImg } from "assets/images";
import { ProjectType } from "types/ProjectType";
import { createProject } from "utils";
import { Qandle_PlayStore_URL, } from "../constants";
import OtherProjects from "./OtherProjects";
import Project from "./Project";
import Heading from "./Heading";

import styles from "./Projects.module.scss";

const blobs = [
  <LightBlueBlob key="1" />,
  <PinkBlob key="2" />,
  <GreenBlob key="3" />,
  <BlueBlob key="4" />,
];

const projects: ProjectType[] = [
  createProject(
    "Qandle Android",
    "Qandle HRMS android application",
    "Qandle android application serves daily 14,000+ users and has 4.5 rating out of 5 in playstore which is max in HRMS category applications",
    QandleAndroidImg,
    Qandle_PlayStore_URL
  ),
];

const Projects = () => {
  return (
    <section id="projects">
      <Heading>Projects</Heading>
      <div className={styles.projects}>
        {projects.map((project, index) => (
          <Project {...project} key={index} blob={blobs[index]} />
        ))}
        <OtherProjects />
      </div>
    </section>
  );
};

export default Projects;
